<template>
	<div class="sub-container">
		<div class="page-title">
			<h2>프로그램</h2>
		</div>
		<div v-if="programList" class="program-list clr">
			<ProgramItem
				v-for="programItem in programList"
				:key="programItem.id"
				:programItem="programItem"
			/>
		</div>
		<div v-else class="program-list clr">
			프로그램이 준비중입니다
		</div>
		<Pagination
			v-if="totalPage !== -1 && programList"
			:listRowCt="15"
			:pageLinkCt="5"
			:totalCount="totalPage"
			:url="'/program'"
		/>
	</div>
</template>

<script>
import { fetchProgram, fetchProgramTotalCount } from '@/api/program';
import Pagination from '@/components/common/Pagination';
import ProgramItem from '@/components/ProgramItem';
export default {
	components: {
		ProgramItem,
		Pagination,
	},
	created() {
		let page = this.$route.params.page - 1 || 0;
		this.fetchProgram(page);
		this.fetchProgramTotalCount();
	},
	data() {
		return {
			current: 1,
			isLoading: false,
			programList: [],
			totalPage: -1,
		};
	},
	methods: {
		async fetchProgram(page) {
			const { data } = await fetchProgram(page);
			this.programList = data.programList;
			console.log(this.programList);
		},
		async fetchProgramTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchProgramTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
	},

	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchProgram(this.currentPage - 1);
			}
		},
	},
};
</script>

